import {
    SET_ALERT,
    REMOVE_ALERT,
} from "../types"

export default (state, action) => {
    switch(action.type) {
        case SET_ALERT:
            // filters by message
            return [...state.filter( alert => alert.msg !== action.payload.msg), action.payload]
        case REMOVE_ALERT:
            return state.filter(alert => alert.desc_key !== action.payload)
        default:
            return state;
    }
}